import React, { useState, useEffect } from 'react';
import { useAccount } from 'wagmi';
import { createClient } from '@supabase/supabase-js';
import type { SocialTask, TaskSubmission } from '@/types/global';
import { 
  Box, 
  Card, 
  CardContent, 
  Typography, 
  Button, 
  Chip, 
  Grid, 
  CircularProgress,
  Tabs,
  Tab,
  Divider,
  Alert,
  IconButton,
  Tooltip
} from '@mui/material';
import {
  Refresh as RefreshIcon,
  CheckCircle as CheckCircleIcon,
  AccessTime as AccessTimeIcon,
  LocalFlorist as FlowerIcon
} from '@mui/icons-material';
import { FaTwitter, FaTelegram, FaDiscord } from 'react-icons/fa';

// ÂàõÂª∫SupabaseÂÆ¢Êà∑Á´Ø
const supabaseUrl = process.env.NEXT_PUBLIC_SUPABASE_URL;
const supabaseAnonKey = process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY;
const supabase = createClient(supabaseUrl, supabaseAnonKey);

// Âπ≥Âè∞ÂõæÊ†áÊò†Â∞Ñ
const platformIcons = {
  twitter: "üê¶",
  telegram: "üì±",
  discord: "üí¨"
};

// ‰ªªÂä°Á±ªÂûãÊò†Â∞Ñ
const taskTypeLabels = {
  twitter_follow: 'ÂÖ≥Ê≥®',
  twitter_like: 'ÁÇπËµû',
  twitter_retweet: 'ËΩ¨Âèë',
  twitter_comment: 'ËØÑËÆ∫',
  telegram_join: 'Âä†ÂÖ•È¢ëÈÅì',
  telegram_message: 'ÂèëÈÄÅÊ∂àÊÅØ',
  discord_join: 'Âä†ÂÖ•ÊúçÂä°Âô®',
  discord_message: 'ÂèëÈÄÅÊ∂àÊÅØ'
};

interface SocialTaskListProps {
  className?: string;
}

/**
 * Á§æ‰∫§‰ªªÂä°ÂàóË°®ÁªÑ‰ª∂
 */
const SocialTaskList: React.FC<SocialTaskListProps> = ({ className }) => {
  const { address, isConnected } = useAccount();
  const [tasks, setTasks] = useState<SocialTask[]>([]);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);
  const [activeTab, setActiveTab] = useState<string>('all');
  const [selectedPlatform, setSelectedPlatform] = useState<string>('all');
  const [userSession, setUserSession] = useState<any>(null);
  const [userFlowerBalance, setUserFlowerBalance] = useState<number>(0);
  const [completingTask, setCompletingTask] = useState<string | null>(null);

  // Ëé∑ÂèñÁî®Êà∑‰ºöËØù
  useEffect(() => {
    const getSession = async () => {
      const { data, error } = await supabase.auth.getSession();
      if (data.session) {
        setUserSession(data.session);
      }
    };

    getSession();
  }, []);

  // Ëé∑ÂèñÁî®Êà∑Â∞èÁ∫¢Ëä±‰ΩôÈ¢ù
  useEffect(() => {
    const fetchUserBalance = async () => {
      if (!userSession) return;

      try {
        const { data, error } = await supabase
          .from('users')
          .select('xiaohonghua_balance')
          .eq('id', userSession.user.id)
          .single();

        if (error) throw error;
        setUserFlowerBalance(data?.xiaohonghua_balance || 0);
      } catch (err) {
        console.error('Ëé∑ÂèñÂ∞èÁ∫¢Ëä±‰ΩôÈ¢ùÂ§±Ë¥•:', err);
      }
    };

    fetchUserBalance();
  }, [userSession]);

  // Âä†ËΩΩ‰ªªÂä°ÂàóË°®
  useEffect(() => {
    fetchTasks();
  }, [activeTab, selectedPlatform, userSession]);

  // Ëé∑Âèñ‰ªªÂä°ÂàóË°®
  const fetchTasks = async () => {
    if (!userSession) return;

    setLoading(true);
    setError(null);

    try {
      // ÊûÑÂª∫API URL
      let url = `${supabaseUrl}/functions/v1/social-tasks/tasks/list?`;
      
      // Ê∑ªÂä†Âπ≥Âè∞Á≠õÈÄâ
      if (selectedPlatform !== 'all') {
        url += `platform=${selectedPlatform}&`;
      }
      
      // Ê∑ªÂä†Áä∂ÊÄÅÁ≠õÈÄâ
      if (activeTab === 'completed') {
        url += 'status=completed&';
      } else {
        url += 'status=active&';
      }
      
      // ÂèëÈÄÅËØ∑Ê±Ç
      const response = await fetch(url, {
        headers: {
          'Authorization': `Bearer ${userSession.access_token}`
        }
      });

      if (!response.ok) {
        throw new Error('Ëé∑Âèñ‰ªªÂä°ÂàóË°®Â§±Ë¥•');
      }

      const result = await response.json();
      
      if (result.success) {
        setTasks(result.data || []);
      } else {
        throw new Error(result.message || 'Ëé∑Âèñ‰ªªÂä°ÂàóË°®Â§±Ë¥•');
      }
    } catch (err) {
      console.error('Ëé∑Âèñ‰ªªÂä°ÂàóË°®ÈîôËØØ:', err);
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  // Â§ÑÁêÜ‰ªªÂä°ÂÆåÊàê
  const handleCompleteTask = async (taskId) => {
    if (!userSession || !isConnected) {
      alert('ËØ∑ÂÖàÁôªÂΩïÂπ∂ËøûÊé•Èí±ÂåÖ');
      return;
    }

    // Ëé∑Âèñ‰ªªÂä°ËØ¶ÊÉÖ
    const task = tasks.find(t => t.id === taskId);
    if (!task) return;

    // Ê£ÄÊü•Â∞èÁ∫¢Ëä±‰ΩôÈ¢ù
    if (userFlowerBalance < task.flower_cost) {
      alert(`Â∞èÁ∫¢Ëä±‰ΩôÈ¢ù‰∏çË∂≥ÔºÅÈúÄË¶Å ${task.flower_cost} ÊúµÂ∞èÁ∫¢Ëä±Ôºå‰ΩÜÊÇ®Âè™Êúâ ${userFlowerBalance} Êúµ„ÄÇ`);
      return;
    }

    setCompletingTask(taskId);

    try {
      // Ê†πÊçÆÂπ≥Âè∞Á±ªÂûãÂ§ÑÁêÜ‰∏çÂêåÁöÑÈ™åËØÅÊµÅÁ®ã
      let platformUserId, platformUsername, proofUrl, proofData;

      if (task.platform === 'twitter') {
        // ÊâìÂºÄTwitterÊéàÊùÉÁ™óÂè£
        const authWindow = window.open(`${window.location.origin}/auth/twitter?task=${taskId}`, 'twitter-auth', 'width=600,height=600');
        
        // Á≠âÂæÖÊéàÊùÉÂÆåÊàê
        const authResult = await new Promise((resolve) => {
          window.addEventListener('message', (event) => {
            if (event.data.type === 'twitter-auth-complete') {
              resolve(event.data);
            }
          });
        });
        
        if (!authResult.success) {
          throw new Error(authResult.message || 'TwitterÊéàÊùÉÂ§±Ë¥•');
        }
        
        platformUserId = authResult.userId;
        platformUsername = authResult.username;
        proofUrl = authResult.proofUrl;
        proofData = { tweetId: authResult.tweetId };
      } else if (task.platform === 'telegram') {
        // ÊâìÂºÄTelegramÊéàÊùÉÁ™óÂè£
        const authWindow = window.open(`${window.location.origin}/auth/telegram?task=${taskId}`, 'telegram-auth', 'width=600,height=600');
        
        // Á≠âÂæÖÊéàÊùÉÂÆåÊàê
        const authResult = await new Promise((resolve) => {
          window.addEventListener('message', (event) => {
            if (event.data.type === 'telegram-auth-complete') {
              resolve(event.data);
            }
          });
        });
        
        if (!authResult.success) {
          throw new Error(authResult.message || 'TelegramÊéàÊùÉÂ§±Ë¥•');
        }
        
        platformUserId = authResult.userId;
        platformUsername = authResult.username;
        proofUrl = authResult.proofUrl;
        proofData = { messageId: authResult.messageId };
      } else if (task.platform === 'discord') {
        // ÊâìÂºÄDiscordÊéàÊùÉÁ™óÂè£
        const authWindow = window.open(`${window.location.origin}/auth/discord?task=${taskId}`, 'discord-auth', 'width=600,height=600');
        
        // Á≠âÂæÖÊéàÊùÉÂÆåÊàê
        const authResult = await new Promise((resolve) => {
          window.addEventListener('message', (event) => {
            if (event.data.type === 'discord-auth-complete') {
              resolve(event.data);
            }
          });
        });
        
        if (!authResult.success) {
          throw new Error(authResult.message || 'DiscordÊéàÊùÉÂ§±Ë¥•');
        }
        
        platformUserId = authResult.userId;
        platformUsername = authResult.username;
        proofUrl = authResult.proofUrl;
        proofData = { messageId: authResult.messageId };
      } else {
        throw new Error('‰∏çÊîØÊåÅÁöÑÂπ≥Âè∞Á±ªÂûã');
      }

      // Êèê‰∫§‰ªªÂä°ÂÆåÊàê
      const response = await fetch(`${supabaseUrl}/functions/v1/social-tasks/tasks/complete`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${userSession.access_token}`
        },
        body: JSON.stringify({
          task_id: taskId,
          platform_user_id: platformUserId,
          platform_username: platformUsername,
          proof_url: proofUrl,
          proof_data: proofData
        })
      });

      const result = await response.json();

      if (!result.success) {
        throw new Error(result.message || 'Êèê‰∫§‰ªªÂä°Â§±Ë¥•');
      }

      // Êõ¥Êñ∞Â∞èÁ∫¢Ëä±‰ΩôÈ¢ù
      setUserFlowerBalance(prev => prev - task.flower_cost + task.flower_reward);
      
      // Âà∑Êñ∞‰ªªÂä°ÂàóË°®
      fetchTasks();
      
      alert(result.message || '‰ªªÂä°ÂÆåÊàêÊàêÂäüÔºÅ');
    } catch (err) {
      console.error('ÂÆåÊàê‰ªªÂä°Â§±Ë¥•:', err);
      alert(`ÂÆåÊàê‰ªªÂä°Â§±Ë¥•: ${err.message}`);
    } finally {
      setCompletingTask(null);
    }
  };

  // Â§ÑÁêÜÊ†áÁ≠æÂàáÊç¢
  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  // Â§ÑÁêÜÂπ≥Âè∞Á≠õÈÄâ
  const handlePlatformChange = (platform) => {
    setSelectedPlatform(platform);
  };

  // Ê∏≤ÊüìÂπ≥Âè∞Á≠õÈÄâÂô®
  const renderPlatformFilter = () => (
    <Box sx={{ display: 'flex', gap: 1, mb: 2 }}>
      <Chip 
        label="ÂÖ®ÈÉ®" 
        color={selectedPlatform === 'all' ? 'primary' : 'default'} 
        onClick={() => handlePlatformChange('all')} 
      />
      <Chip
        icon={<FaTwitter />}
        label="Twitter"
        color={selectedPlatform === 'twitter' ? 'primary' : 'default'}
        onClick={() => handlePlatformChange('twitter')}
      />
      <Chip
        icon={<FaTelegram />}
        label="Telegram"
        color={selectedPlatform === 'telegram' ? 'primary' : 'default'}
        onClick={() => handlePlatformChange('telegram')}
      />
      <Chip
        icon={<FaDiscord />}
        label="Discord"
        color={selectedPlatform === 'discord' ? 'primary' : 'default'}
        onClick={() => handlePlatformChange('discord')}
      />
    </Box>
  );

  // Ê∏≤Êüì‰ªªÂä°Âç°Áâá
  const renderTaskCard = (task) => (
    <Card key={task.id} sx={{ mb: 2, position: 'relative' }}>
      <CardContent>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 1 }}>
          <Typography variant="h6" component="div">
            {task.title}
          </Typography>
          <Chip 
            icon={platformIcons[task.platform]} 
            label={task.platform.charAt(0).toUpperCase() + task.platform.slice(1)} 
            size="small" 
            color="primary" 
            variant="outlined" 
          />
        </Box>
        
        <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>
          {task.description}
        </Typography>
        
        <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
          <Chip 
            label={taskTypeLabels[task.task_type] || task.task_type} 
            size="small" 
            sx={{ mr: 1 }} 
          />
          <Tooltip title="ÊØèÊó•ÂèØÂÆåÊàêÊ¨°Êï∞">
            <Chip 
              icon={<AccessTimeIcon fontSize="small" />}
              label={`ÊØèÊó•ÈôêÂà∂: ${task.daily_limit}Ê¨°`} 
              size="small" 
              variant="outlined" 
              sx={{ mr: 1 }} 
            />
          </Tooltip>
        </Box>
        
        <Divider sx={{ my: 1 }} />
        
        <Grid container spacing={2} sx={{ mt: 1 }}>
          <Grid item xs={6}>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <FlowerIcon color="error" fontSize="small" sx={{ mr: 0.5 }} />
              <Typography variant="body2" color="error">
                Ê∂àËÄó: {task.flower_cost} Â∞èÁ∫¢Ëä±
              </Typography>
            </Box>
          </Grid>
          <Grid item xs={6}>
            <Box sx={{ display: 'flex', alignItems: 'center' }}>
              <FlowerIcon color="success" fontSize="small" sx={{ mr: 0.5 }} />
              <Typography variant="body2" color="success">
                Â•ñÂä±: {task.flower_reward} Â∞èÁ∫¢Ëä±
              </Typography>
            </Box>
          </Grid>
        </Grid>
        
        <Box sx={{ display: 'flex', justifyContent: 'flex-end', mt: 2 }}>
          {task.isCompleted ? (
            <Button 
              variant="contained" 
              color="success" 
              startIcon={<CheckCircleIcon />}
              disabled
            >
              Â∑≤ÂÆåÊàê
            </Button>
          ) : (
            <Button 
              variant="contained" 
              color="primary"
              onClick={() => handleCompleteTask(task.id)}
              disabled={completingTask === task.id}
            >
              {completingTask === task.id ? (
                <>
                  <CircularProgress size={20} sx={{ mr: 1 }} />
                  Â§ÑÁêÜ‰∏≠...
                </>
              ) : 'ÂÆåÊàê‰ªªÂä°'}
            </Button>
          )}
        </Box>
      </CardContent>
    </Card>
  );

  return (
    <Box>
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 3 }}>
        <Typography variant="h5" component="h2">
          Á§æ‰∫§‰ªªÂä°
        </Typography>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <Chip 
            icon={<FlowerIcon />} 
            label={`${userFlowerBalance} Â∞èÁ∫¢Ëä±`} 
            color="primary" 
            sx={{ mr: 2 }} 
          />
          <IconButton onClick={fetchTasks} disabled={loading}>
            <RefreshIcon />
          </IconButton>
        </Box>
      </Box>

      <Tabs value={activeTab} onChange={handleTabChange} sx={{ mb: 2 }}>
        <Tab label="ÂèØÁî®‰ªªÂä°" value="all" />
        <Tab label="Â∑≤ÂÆåÊàê" value="completed" />
      </Tabs>

      {renderPlatformFilter()}

      {error && (
        <Alert severity="error" sx={{ mb: 2 }}>
          {error}
        </Alert>
      )}

      {loading ? (
        <Box sx={{ display: 'flex', justifyContent: 'center', my: 4 }}>
          <CircularProgress />
        </Box>
      ) : tasks.length > 0 ? (
        tasks.map(renderTaskCard)
      ) : (
        <Alert severity="info">
          {activeTab === 'completed' ? 'ÊÇ®ËøòÊ≤°ÊúâÂÆåÊàê‰ªª‰Ωï‰ªªÂä°' : 'ÂΩìÂâçÊ≤°ÊúâÂèØÁî®ÁöÑ‰ªªÂä°'}
        </Alert>
      )}
    </Box>
  );
};

export default SocialTaskList;
